<template>
    <section>
      <!-- left side image -->
      <a v-if="!info.preview.isRight && !info.preview.lottie" target="_blank" rel="noopener noreferrer" :href='info.preview.link' class="image"><img :src='require(`@/assets/images/${info.preview.img}`)' alt="" :data-position="previewPosition" /></a>
      <a v-if="info.preview.lottie" target="_blank" rel="noopener noreferrer" :href='info.preview.link' class="image"><div :id="'lottie-'+info.preview.lottieId"></div></a>

      <div class="content">
        <div class="inner">
          <h2>{{info.title}}</h2>
          <h3>{{info.subtitle}}</h3>
          <h5 v-if='info.subsubtitle'>{{info.subsubtitle}}</h5>
          <p v-html='info.bodyHtml'></p>
          <ul class="actions">
            <li v-for='button in info.buttons' :key='button.text'><a target="_blank" rel="noopener noreferrer" :href='button.link' class="button" :class='{disabled: button.disabled}' >{{button.text}}</a></li>
          </ul>
        </div>
      </div>
      <!-- right side image -->
      <a v-if="info.preview.isRight && !info.preview.lottie" target="_blank" rel="noopener noreferrer" :href='info.preview.link' class="image"><img :src='require(`@/assets/images/${info.preview.img}`)' alt="" :data-position="previewPosition" /></a>
    </section>
</template>

<script>

export default {

  props: ['info'],

  computed: {
    previewPosition() {
      return (this.info.preview.position)? this.info.preview.position : "center center"
    }
  }

}
</script>

<style lang="scss">
  #lottie-playwell {
    background-color: rgb(56, 4, 57);
    width: 100%;
    height: 100%;

    svg {
      transform: scale(1.4) !important;
    }
  }</style>