<template>
  <div id="lunarch">
    
		<!-- Header -->
			<header id="header">
				<a href="index.html" class="title">Amon's website</a>
				<nav>
					<ul>
						<li><a href="index.html">Home</a></li>
						<li><a href="lunarch.html" class="active" style="pointer-events:none;">Lunarch</a></li>
					</ul>
				</nav>
			</header>

    <!-- Wrapper -->
    <div id="wrapper">

      <!-- Main -->
        <section id="main" class="wrapper">
          <div class="inner">
            <h1 class="major">Hi Elyot &amp; Lunarch!</h1>
            <form action="" id="unlock" v-if="showUnlock">
            <p>To decrypt this page, submit the password I gave you below:</p>
            <div class="unlock">
              <input v-model="pw" 
                type="text" 
                id="password"
                ref="password"
                placeholder="Password" 
                maxlength="30"
                :disabled="disableUnlock"
                autofocus
                />
              <input type="submit" 
              value="Submit"
              id="submit"
              @click.capture.prevent="unlock"
              :disabled="disableUnlock"
              >
            </div>
            </form>
            <div id="content" v-html="content"></div>
          </div>
        </section>
    </div>
  </div>
</template>

<style lang="scss">
  #lunarch p, #lunarch span {
    font-size: 16.5pt;
  }

  #lunarch p {
    margin: 0 0 1em 0;
    font-size: 16.5pt;
  }

  #lunarch h1.major {
    margin: 0 0 1.1rem 0;
  }

  #password {
    font-size: 16.5pt;
    min-width: 40%;
    max-width: 400px;
    height: 60px;
  }

  #submit {
    height: 60px;
    margin: 0 0 0 20px;
    font-size: 8pt;
  }

  .unlock {
    display: flex;
    align-items: center;
  }

  .sign {
    font-style: italic;
  }

  .puzzles {
    padding: 0 0 2rem 1.5rem;
    display: flex;
    flex-direction: column;
  }

  .puzzle {
    display: flex;
    padding: 1rem 0 0 0;
  }

  .puzzle .thumb {
    margin: 0.3rem 1rem;
    min-width: 80px;
    min-height: 80px;
    width: 80px;
    height: 80px;
    background-size: contain;
  }

  .puzzle .oneliner {
    font-size: 16pt;
    padding: 0.3rem 1rem;
    display: flex;
    align-items: center;
    // height: 80px;
    justify-content: center;
  }
</style>

<script>

import {decrypt} from 'triplesec'
import content from '@/assets/lunarch/content.json'

export default {
  
  data: () => {
    return {
      showUnlock: true,
      disableUnlock: true,
      pw: '',
      content: '',
      ciphertext: null,
    }
  },

  mounted () {
    // prepare ciphertext
    this.ciphertext = Buffer.from(content.encrypted, "hex")

    this.disableUnlock = false
    this.$refs.password.disabled = false
    this.$refs.password.focus()

  },

  methods: {
    async decryptAsync(obj) {
      return new Promise((res, rej) => {
        decrypt(obj, (err, pt)=>{
          if (err) {
            rej(err)
          }
          res(pt)
        })
      })
    },

    async unlock() {
      this.disableUnlock = true
      // try decrypt
      let plaintext

      try {
        const key = Buffer.from(this.pw)

        plaintext = await this.decryptAsync({key:key, data:this.ciphertext})
      } catch (err) {
        this.pw = ""
        this.content = '<h4><i>Incorrect password!</i></h4>'
        this.disableUnlock = false
        this.$refs.password.disabled = false
        this.$refs.password.focus()
        return
      } 
      this.content = plaintext.toString()
      this.showUnlock = false
    
    }
  },

}
</script>